import React, { useState } from "react";
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

function SelectMonth(props) {
    const [month, setMonth] = useState("");
  
    const handleMonthChange = (e) => {
        const selectedMonth = e.target.value;
        setMonth(selectedMonth);
        props.onSelect(selectedMonth)
    };
  
    return (
        <div>
            <FormControl sx={{ mt: 1, minWidth: 260, textAlign: 'right' }} size="small">
                <InputLabel id="demo-simple-select-helper-label"> </InputLabel>
                <Select
                    labelId="demo-simple-select-helper-label"
                    id="demo-simple-select-helper"
                    value={month}
                    label=" "
                    onChange={handleMonthChange}
                >
                    <MenuItem value="">
                        <em>None</em>
                    </MenuItem>
                    <MenuItem value={1}></MenuItem>
                    <MenuItem value={2}></MenuItem>
                    <MenuItem value={3}></MenuItem>
                    <MenuItem value={4}></MenuItem>
                    <MenuItem value={5}></MenuItem>
                    <MenuItem value={6}></MenuItem>
                    <MenuItem value={7}></MenuItem>
                    <MenuItem value={8}></MenuItem>
                    <MenuItem value={9}></MenuItem>
                    <MenuItem value={10}></MenuItem>
                    <MenuItem value={11}></MenuItem>
                    <MenuItem value={12}></MenuItem>
                </Select>
               
            </FormControl>
        </div>
    );
}

export default SelectMonth;